    // 加减购物车物品数量
    controlNum(good, index, type) {
      console.log(good, index, type)
      let that = this
      let url = that.$api.addcar
      let num = that.list[0].goodArr[index].total_num
      let data = {
        goods_id: good.goods_id,
        goods_sku_id: good.goods_sku_id
      }
      // 减少数量
      if(type === 'cut' && num > 1) {
        console.log('进来了减少', type, num)
        url = that.$api.subcar
        that.$http({
          url: url,
          methods: 'POST',
          data: data,
          cb: (err, res)=> {
            if(!err && res.code === 1) {
              that.list[0].goodArr[index].total_num -= 1
              return
            }
          }
        })
      }
      // 增加数量
      if(type === 'add') {
        console.log('进来了增加')
        data.goods_num = 1
        that.$http({
          url: url,
          methods: 'POST',
          data: data,
          cb: (err, res)=> {
            if(!err && res.code === 1) {
              that.list[0].goodArr[index].total_num += 1
            } else if(res.code === 0) {
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            }
          }
        })
        return
      }
    },
    
    goShopping() {
      uni.switchTab({
        url: '../market/market'
      })
    },
    goDetail(goods_id) {
      uni.navigateTo({
        url: '../components/goodDetail?goods_id=' + goods_id
      })
    }
